import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {Icon, Select, Tooltip} from "antd";
import classNames from "classnames";
import digidWorksLogo from "../../base/assets/images/digid-works-logo.png";
import {DefaultFooter} from "../../base/components/layouts/DefaultFooter";
import DefaultMenu from "../../base/components/menus/DefaultMenu";
import {FormattedMessage, injectIntl} from "react-intl";
import DefaultLogoutButton from "../../base/components/auth/DefaultLogoutButton";
import {inject, observer} from "mobx-react";
import {availableCurrencies, availableLocales} from "../../localizations";
import {FlexCol, FlexRow} from "../../base/components/layouts/FlexGrid";
import TutorialTip from "../../base/components/data-display/TutorialTip";
import {availableThemes} from "../../configurations/ui";

/**
 * Default layout.
 */
@inject("context")
@injectIntl
@observer
class MenusLayout extends Component {
	constructor(props) {
		super(props);

		this.state = {
			sidebarHidden: false
		};
	}

	render() {
		const {sidebarHidden} = this.state;
		const {intl} = this.props;

		const {theme, currency, locale} = this.props.context.appService.settings;

		return <div className={classNames("application-container", {
			"sidebar-hidden": sidebarHidden,
		})}>
			<div className="sidebar">
				<Link style={{display: "block"}} to="/">
					<span style={{color: "#2dcd4d", fontSize: "3rem", fontFamily: "Mansalva", lineHeight: 1}}>
						<FormattedMessage id="application.title"/>
					</span>
					<div className="d-flex justify-content-end align-items-start">
						<span className="font-weight-bold slogan-regular-text">
							<FormattedMessage id="by"/>
						</span>
						<img style={{maxWidth: "120px"}}
							 src={digidWorksLogo} title={intl.formatMessage({id: "application.digid.logo.title"})} alt={intl.formatMessage({id: "application.digid.logo.title"})}/>
					</div>
				</Link>
				<DefaultMenu
					menuProps={{
						style: {
							marginTop: "20px"
						}
					}}
					menuItemProps={{
						style: {
							margin: 0,
							padding: 0,
						}
					}}
				/>
			</div>
			<div className="application-layout">
				<div className="application-header">
					<FlexRow>
						<FlexCol className="d-flex align-items-center">
							<Tooltip title={<FormattedMessage id="tooltips.sidebarMenuTrigger"/>}>
								<Icon
									type={sidebarHidden ? 'menu-unfold' : 'menu-fold'}
									onClick={this.handleSideMenuTriggerClick}
									style={{
										fontSize: "25px",
										alignSelf: "center",
									}}
								/>
							</Tooltip>
						</FlexCol>
						<FlexCol className="d-flex ml-auto align-items-center" xs="auto">
							<TutorialTip title={<FormattedMessage id="tooltips.websiteTheme"/>} placement="bottom">
								<Select className="borderless-select"
										defaultValue={theme}
										onChange={this.handleThemeSelect}
										dropdownMatchSelectWidth={false}>
									{Object.entries(availableThemes).map(([key, value]) => <Select.Option key={key} value={key}><FormattedMessage id="application.themes.theme"/>: <FormattedMessage id={value}/></Select.Option>)}
								</Select>
							</TutorialTip>
						</FlexCol>
						<FlexCol className="d-flex align-items-center" xs="auto">
							<TutorialTip title={<FormattedMessage id="tooltips.websiteCurrency"/>} placement="bottom">
								<Select className="borderless-select"
										defaultValue={currency}
										onChange={this.handleCurrencySelect}
										dropdownMatchSelectWidth={false}>
									{Object.entries(availableCurrencies).map(([key, value]) => <Select.Option key={key} value={key}><FormattedMessage id={value}/></Select.Option>)}
								</Select>
							</TutorialTip>
						</FlexCol>
						<FlexCol className="d-flex align-items-center" xs="auto">
							<TutorialTip title={<FormattedMessage id="tooltips.websiteLocale"/>} placement="bottom">
								<Select className="borderless-select"
										defaultValue={locale}
										onChange={this.handleLocaleSelect}
										dropdownMatchSelectWidth={false}>
									{Object.entries(availableLocales).map(([key, value]) => <Select.Option key={key} value={key}><FormattedMessage id={value}/></Select.Option>)}
								</Select>
							</TutorialTip>
						</FlexCol>
						<FlexCol className="d-flex align-items-center" xs="auto">
							<TutorialTip title={<FormattedMessage id="tooltips.logoutButton"/>} placement="bottomRight">
								<DefaultLogoutButton style={{fontSize: "25px", alignSelf: "center",}}
													 title={this.props.intl.formatMessage({id: "logout"})}/>
							</TutorialTip>
						</FlexCol>
					</FlexRow>
				</div>
				<div className="application-content">
					{this.props.children}
				</div>
				<DefaultFooter/>
			</div>
		</div>
	}

	handleSideMenuTriggerClick = () => {
		this.setState({sidebarHidden: !this.state.sidebarHidden});
	};

	handleThemeSelect = (value) => {
		this.props.context.appService.changeTheme(value);
	};

	handleCurrencySelect = (value) => {
		this.props.context.appService.changeCurrency(value);
	};

	handleLocaleSelect = (value) => {
		this.props.context.appService.changeSetting("locale", value);
	};
}

export default MenusLayout;
