import bg_BG from "./bg-BG";
import en_US from "./en-US";
import {intl} from "../base/localizations";
import {merge} from "lodash";

export const appTranslations = merge({}, intl, {
	"bg-BG": bg_BG,
	// "de-DE": de_DE,
	"en-US": en_US
});

export const availableLocales = {
	"bg-BG": "application.locales.bg",
	// "de-DE": "application.locales.de",
	"en-US": "application.locales.en"
};

export const availableCurrencies = {
	"EUR": "application.currencies.eur",
	"USD": "application.currencies.usd",
	"CAD": "application.currencies.cad",
	"GBP": "application.currencies.gbp",
};

let userLocale = navigator.languages ? navigator.languages[0] : (navigator.language || navigator.userLanguage);

export const defaultLocale = (userLocale === "bg" ? "bg-BG" : "en-US");
export const defaultCurrency = "EUR";
